.CodeMirror .cm-spell-error:not(.cm-url):not(.cm-comment):not(.cm-tag):not(.cm-word) {
  background: none !important;
}
.article-main {
  margin: 0 auto;
  font-family: 'Lato', 'PingFang SC', 'Microsoft YaHei', sans-serif;
  color: #555;
  line-height: 2;
}
.article-main img {
  max-width: 100%;
  margin: 0 auto 25px;
  box-sizing: border-box;
  padding: 3px;
  border: 1px solid #ddd;
}
.article-main pre,
.article-main code {
  font-family: consolas, Menlo, 'PingFang SC', 'Microsoft YaHei', monospace;
}
.article-main code {
  padding: 2px 4px;
  word-wrap: break-word;
  color: #ff502c;
  background: #fff5f5;
  border-radius: 3px;
  font-size: 13px;
}
.article-main pre {
  padding: 10px;
  overflow: auto;
  margin: 20px 0;
  font-size: 13px;
  color: #4d4d4c;
  background: #f7f7f7;
  line-height: 1.6;
}
.article-main pre code {
  padding: 0;
  color: #555;
  background: none;
  text-shadow: none;
  font-family: consolas, Menlo, 'PingFang SC', 'Microsoft YaHei', monospace;
}
.article-main h2,
.article-main h3,
.article-main h4,
.article-main h5,
.article-main h6 {
  margin: 20px 0 15px 0;
  padding-top: 10px;
  border-bottom: 1px solid #eee;
  margin-bottom: 10px;
  font-weight: bold;
  line-height: 1.5;
  font-family: 'Lato', 'PingFang SC', 'Microsoft YaHei', sans-serif;
  color: #555;
}
.article-main h2 {
  font-size: 1.4em;
}
.article-main h3 {
  font-size: 1.3em;
  border-bottom: 1px dotted #eee;
}
.article-main h4 {
  font-size: 1.2em;
}
.article-main ul {
  padding-left: 20px;
}
.article-main ul li {
  line-height: 2;
  list-style: circle;
}
.article-main .hljs-comment,
.article-main .hljs-quote {
  color: #575f6a;
}
.article-main blockquote {
  margin: 1em 0;
  border-left: 4px solid #ddd;
  padding: 0 1em;
  color: #666;
}
.article-main blockquote p {
  margin: 0.5em 0;
  line-height: 1.7em;
}
.article-main table {
  font-size: 0.8em;
  max-width: 100%;
  overflow: auto;
  border: 1px solid #f6f6f6;
  border-collapse: collapse;
  border-spacing: 0;
}
.article-main table thead {
  background: #f6f6f6;
  color: #000;
  text-align: left;
}
.article-main table tr {
  display: table-row;
  vertical-align: inherit;
  border-color: inherit;
}
.article-main table tr:nth-child(2n) {
  background-color: #fcfcfc;
}
.article-main table th {
  padding: 0.8em 0.5em;
  line-height: 1.5em;
}
.article-main table tbody {
  display: table-row-group;
  vertical-align: middle;
  border-color: inherit;
}
.article-main table tbody td {
  min-width: 7.5em;
  padding: 0.8em 0.5em;
  line-height: 1.5em;
}
.article-main ol {
  list-style: decimal;
  margin: 5px 0 5px 15px;
}
